.card {
	display: flex;
	align-items: center;
	width: 100%;
	padding: 8px 10px;
	margin: 2px 0;
	background-color: white;
	border-radius: 5px 5px 0 0;
	border-bottom: 1px solid rgb(225, 223, 223);
	z-index: 1;
	cursor: grab;

	&.active {
		border: 1px solid #e983dd30;
		background-image: linear-gradient(to right, rgba(233, 131, 221, 0.05), rgba(87, 158, 191, 0.05));
		border-radius: 5px;
		position: relative;
	}
}
