.todoContainer {
	display: flex;
	flex-direction: column;
	align-items: center;
	background-image: linear-gradient(to bottom, #e983dd80, #579ebfcc), url('../../../public/images/header.jpg');
	height: 280px;
	background-size: cover;
	opacity: 0.9;

	.todoSection {
		width: 50vw;
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		justify-content: flex-start;
		padding: 3rem 0;

		.todoHeader {
			color: #ffffff;
			font-size: 3rem;
			letter-spacing: 1rem;
			margin-bottom: 2rem;
		}

		.todoList {
			width: 100%;
			background-color: white;
			border-radius: 5px;
		}

		.todoNote {
			color: grey;
			font-size: 14px;
			margin-top: 3rem;
			align-self: center;
		}
	}

	@media (max-width: 768px) {
		.todoSection {
			width: 90vw;
		}
	}
}
